.popular-movies {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-column-gap: 1rem;
    grid-row-gap: 2rem;
}

img {
    width: 100%;
    height: 30vh;
    object-fit: cover;
    border-radius: 1rem;
    margin-bottom: 1rem;
}

h2 {
    font-size: 0.8rem;
}

.filter-container button {
    margin-right: 2rem;
    min-width: 5rem;
    padding: 0.5rem 1rem;
    border: none;
    background-color: rgb(129, 218, 210);
    color: white;
    border-radius: 1rem;
    border: 2px solid rgb(28, 36, 56);
    font-weight: bold;
    cursor: pointer;
}

.filter-container button.active {
    background-color: white;
    color: rgb(129, 218, 210);
}